import CpForm from './CpForm'

export default {
  components: {
    CpForm,
  },
  props: {
    cProp: {
      required: true,
      default: {},
      // ?? TODO
      // default() {
      //   return {}
      // },
    },
  },
  methods: {
    onEdit(toggle) {
      this.editMode = toggle
    },
    validate() {
      return this.$validator.validateAll()
    },
    cancel() {
      this.cpModel = Object.assign({}, this.cProp)
      this.$emit('cancel')
    },
  },
}



