import Vue from 'vue'
import Router from 'vue-router'

import {requireUser} from './guard'

import authRouter from './authRouter'
import companyRouter from './companyRouter'

// Pages
// import HomePage from '@/pages/HomePage'
import CompaniesPage from '@/pages/CompaniesPage'
import AddCompanyPage from '@/pages/AddCompanyPage'
import MatchingToolPage from '@/pages/MatchingToolPage'

// AUTH Pages
import CompanyPage from '@/pages/company/CompanyPage'


// Prototype Pages
import PrototypePage from '@/pages/prototypes/PrototypePage'
import FxRatesPage from '@/pages/prototypes/FxRatesPage'
import NomenclaturesPage from '@/pages/prototypes/NomenclaturesPage'


Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: CompaniesPage,
      meta: {
        pageId: 'home-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    ...authRouter,
    {
      path: '/companies/:id',
      name: 'CompanyPage',
      component: CompanyPage,
      props: true,
      redirect: '/companies/:id/profile',
      meta: {
        pageId: 'company-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
      children: companyRouter,
    },

    // {
    //   path: '/companies',
    //   name: 'Companies',
    //   component: CompaniesPage,
    //   meta: {
    //     pageId: 'companies-page',
    //     pageClass: '',
    //   },
    //   beforeEnter: requireUser,
    // },
    {
      path: '/add-company',
      name: 'AddCompany',
      component: AddCompanyPage,
      meta: {
        pageId: 'add-company-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/matching-tool',
      name: 'MatchingTool',
      component: MatchingToolPage,
      meta: {
        pageId: 'matching-tool-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/prototype',
      name: 'Prototype',
      component: PrototypePage,
      redirect: '/prototype/company',
      meta: {
        pageId: '',
        pageClass: 'prototype-page',
      },
      beforeEnter: requireUser,
      children: [
        {
          path: 'company',
          name: 'Company',
          component: CompanyPage,
          meta: {
            pageId: 'company-page',
            pageClass: '',
          },
          beforeEnter: requireUser,
        },
        {
          path: 'fx',
          name: 'FxRates',
          component: FxRatesPage,
          meta: {
            pageId: 'fx-page',
            pageClass: '',
          },
          beforeEnter: requireUser,
        },
        {
          path: 'nomenclatures',
          name: 'Nomenclatures',
          component: NomenclaturesPage,
          meta: {
            pageId: 'nomenclatures-page',
            pageClass: '',
          },
          beforeEnter: requireUser,
        },
      ],
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
  mode: 'history',
  linkExactActiveClass: 'active',
})



