import companyService from '@/services/companyService'
import aliasService from '@/services/aliasService'
import personnelService from '@/services/personnelService'
import vatService from '@/services/vatService'
import companyPropertyService from '@/services/companyPropertyService'
import {COMPANY_PROPERTY_CATEGORIES} from '@/constants'


const state = {
  selectedCompany: null,
  myCompanies: [],
}


const getters = {
  selectedCompany: (state) => state.selectedCompany,
  myCompanies: (state) => state.myCompanies,
}


const mutations = {
  setMyCompanies: (state, payload) => { state.myCompanies = payload },

  // CRUD
  addCompany: (state, payload) => {
    // TODO: push after its parent?
    state.myCompanies.push(payload)
  },
  setSelectedCompany: (state, payload) => {
    state.selectedCompany = payload
    if (state.myCompanies) {
      const index = state.myCompanies.findIndex(company => company.id === payload.id)
      if (index > -1) { state.myCompanies = payload }
    }
  },
  deleteSelCompany: (state) => {
    if (state.myCompanies) {
      const index = state.myCompanies.indexOf(state.selectedCompany)
      if (index > -1) { state.myCompanies.splice(index, 1) }
    }
    state.selectedCompany = null
  },

  // SELECTED COMPANY PROPERTIES

  setSCLogo: (state, payload) => { state.selectedCompany.logo.URL = payload },

  // ALIASES
  addSCAlias: (state, payload) => state.selectedCompany.aliases.push(payload),
  updateSCAlias: (state, {alias, updatedAname}) => {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(alias)
    aliases[index].aname = updatedAname
  },
  deleteSCAlias: (state, payload) => {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(payload)
    aliases.splice(index, 1)
  },
  // VATs
  addSCVat: (state, payload) => state.selectedCompany.vats.push(payload),
  updateSCVat: (state, payload) => {
    var vats = state.selectedCompany.vats
    const index = vats.findIndex(item => item.id === payload.id)
    vats[index] = payload
  },
  deleteSCVat: (state, payload) => {
    var vats = state.selectedCompany.vats
    const index = vats.indexOf(payload)
    vats.splice(index, 1)
  },

  // PERSONNEL
  addSCPersonnel: (state, payload) => state.selectedCompany.personnel.push(payload),
  updateSCPersonnel: (state, payload) => {
    var personnel = state.selectedCompany.personnel
    const index = personnel.findIndex(item => item.id === payload.id)
    personnel[index] = payload
  },
  deleteSCPersonnel: (state, payload) => {
    var personnel = state.selectedCompany.personnel
    const index = personnel.indexOf(payload)
    personnel.splice(index, 1)
  },

  // COMPANY PROPERTY PAIR
  addSCCPPair: (state, payload) => {
    var catName = getCategoryName(payload)
    state.selectedCompany[catName].push(payload)
  },
  updateSCCPPair: (state, payload) => {
    var catName = getCategoryName(payload)
    var category = state.selectedCompany[catName]
    const index = category.findIndex(item => item.id === payload.id)
    category[index] = payload
  },
  deleteSCCPPair: (state, payload) => {
    var catName = getCategoryName(payload)
    var category = state.selectedCompany[catName]
    const index = category.indexOf(payload)
    category.splice(index, 1)
  },
}

function getCategoryName(cpPair) {
  var catName = Object.keys(COMPANY_PROPERTY_CATEGORIES).find(item => COMPANY_PROPERTY_CATEGORIES[item] === cpPair.category.id)
  if (catName === 'tax') {
    catName = 'taxes'
  }
  return catName
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  loadMyCompanies({ commit, state }) {
    // if (state.myCompanies) {
    //   return Promise.resolve(state.myCompanies)
    // }

    return companyService.getMyCompanies()
      .then(thenHandler(commit, 'setMyCompanies'))
  },

  // CRUD
  createCompany({ commit }, companyData) {
    return companyService.create(companyData)
      .then(thenHandler(commit, 'addCompany'))
  },
  loadSelCompany({ commit, state }, id) {
    if (state.selectedCompany && state.selectedCompany.id === id) {
      return Promise.resolve(state.selectedCompany)
    }

    return companyService.getCompany({id})
      .then(thenHandler(commit, 'setSelectedCompany'))
  },
  updateSelCompany({ commit, state }, updatedData) {
    return companyService.update(state.selectedCompany, updatedData)
      .then(company => {
        commit('setSelectedCompany', company)
        return company
      })
  },
  deleteSelCompany({ commit, state }) {
    return companyService.delete(state.selectedCompany)
      .then(() => {
        commit('deleteSelCompany')
      })
  },

  // SELECTED COMPANY PROPERTIES

  uploadSCLogo({ commit, state }, imgFile) {
    return companyService.uploadLogo(state.selectedCompany, imgFile)
      .then(thenHandler(commit, 'setSCLogo'))
  },


  // ALIASES
  createSCAlias({ commit, state }, aname) {
    return aliasService.create(state.selectedCompany, state.selectedCompany, aname)
      .then(thenHandler(commit, 'addSCAlias'))
  },
  updateSCAlias({ commit }, {alias, updatedAname}) {
    return aliasService.update(alias, updatedAname)
      .then(() => {
        commit('updateSCAlias', {alias, updatedAname})
        return true
      })
  },
  deleteSCAlias({ commit }, model) {
    return aliasService.delete(model)
      .then(() => {
        commit('deleteSCAlias', model)
      })
  },

  // TODO: refactor - generate some of the actions andmutations

  // VAT
  createSCVat({ commit, state }, modelData) {
    return vatService.create(state.selectedCompany, modelData)
      .then(thenHandler(commit, 'addSCVat'))
  },
  updateSCVat({ commit }, newModel) {
    return vatService.update(newModel)
      .then(() => {
        commit('updateSCVat', newModel)
        return true
      })
  },
  deleteSCVat({ commit }, model) {
    return vatService.delete(model)
      .then(() => {
        commit('deleteSCVat', model)
      })
  },


  // PERSONNEL
  createSCPersonnel({ commit, state }, modelData) {
    return personnelService.create(state.selectedCompany, modelData)
      .then(thenHandler(commit, 'addSCPersonnel'))
  },
  updateSCPersonnel({ commit }, newModel) {
    return personnelService.update(newModel)
      .then(() => {
        commit('updateSCPersonnel', newModel)
        return true
      })
  },
  deleteSCPersonnel({ commit }, model) {
    return personnelService.delete(model)
      .then(() => {
        commit('deleteSCPersonnel', model)
      })
  },


  // COMPANY PROPERTY PAIR
  createSCCPPair({ commit, state }, modelData) {
    return companyPropertyService.create(state.selectedCompany, modelData)
      .then(thenHandler(commit, 'addSCCPPair'))
  },
  updateSCCPPair({ commit }, newModel) {
    return companyPropertyService.update(newModel)
      .then(() => {
        commit('updateSCCPPair', newModel)
        return true
      })
  },
  deleteSCCPPair({ commit }, model) {
    return companyPropertyService.delete(model)
      .then(() => {
        commit('deleteSCCPPair', model)
      })
  },
}


export default {
  state,
  getters,
  mutations,
  actions,
}



